// src/app/admin/program/page.tsx
"use client";
import React, { useEffect, useState } from "react";
import Link from "next/link";
import Cookies from "js-cookie";
import { Plus, Pencil, Trash2, Loader2, CheckCircle, XCircle, Calendar } from "lucide-react";
import { Program } from "@/types";
import { cn } from "@/lib/utils";

export default function AdminProgramPage() {
  const [programs, setPrograms] = useState<Program[]>([]);
  const [loading, setLoading] = useState(true);
  const [userRole, setUserRole] = useState("");

  // 1. Ambil URL Backend dari Environment
  const baseUrl = process.env.NEXT_PUBLIC_BASE_URL || "http://localhost:3000";

  // Ambil data & cek role
  const initData = async () => {
    // Ambil Role & Token dari Cookie
    const role = Cookies.get("user_role") || "";
    const token = Cookies.get("token");
    setUserRole(role);

    try {
      // 2. Gunakan baseUrl dinamis & Kirim Token
      // Endpoint: /api/program
      const res = await fetch(`${baseUrl}/api/program`, { 
        cache: "no-store",
        headers: {
            "Content-Type": "application/json",
            "Authorization": `Bearer ${token}` // <--- PENTING: Kirim token saat GET
        }
      });
      
      if (!res.ok) throw new Error("Gagal ambil data program");
      setPrograms(await res.json());

    } catch (error) {
      console.error(error);
    } finally {
      setLoading(false);
    }
  };

  // Fungsi Publish / Reject
  const handleStatusChange = async (id: string, newStatus: string) => {
    const token = Cookies.get("token");
    try {
      // Perbaiki URL Endpoint
      const res = await fetch(`${baseUrl}/api/program/${id}`, {
        method: "PATCH", 
        headers: {
          "Content-Type": "application/json",
          "Authorization": `Bearer ${token}`
        },
        body: JSON.stringify({ status: newStatus }) 
      });

      if (res.ok) {
        // alert(`Status berhasil diubah ke ${newStatus}`); // Optional alert
        initData(); // Refresh tabel
      } else {
        alert("Gagal mengubah status");
      }
    } catch (error) {
      alert("Terjadi kesalahan koneksi");
    }
  };

  const handleDelete = async (id: string) => {
    if (!confirm("Hapus program ini permanen?")) return;
    const token = Cookies.get("token");
    
    // Perbaiki URL Endpoint
    const res = await fetch(`${baseUrl}/api/program/${id}`, {
      method: "DELETE",
      headers: { "Authorization": `Bearer ${token}` }
    });
    if (res.ok) initData();
  };

  useEffect(() => {
    initData();
  }, []);

  return (
    <div>
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-bold font-heading">Program Kerja</h1>
          <p className="text-gray-400 text-sm">
             Login sebagai: <span className={cn("font-bold uppercase", userRole === 'SUPER_ADMIN' ? "text-red-500" : "text-blue-500")}>{userRole.replace('_', ' ')}</span>
          </p>
        </div>
        <Link href="/admin/program/create" className="bg-blue-600 hover:bg-blue-700 text-white px-4 py-2 rounded-lg flex items-center gap-2 font-bold text-sm transition-colors">
          <Plus size={18} /> TAMBAH PROGRAM
        </Link>
      </div>

      <div className="bg-zinc-900 border border-white/10 rounded-xl overflow-hidden shadow-xl">
        {loading ? (
            <div className="p-12 text-center flex flex-col items-center gap-3 text-gray-500">
                <Loader2 className="animate-spin w-8 h-8 text-blue-500" />
                <span className="text-sm">Memuat program kerja...</span>
            </div>
        ) : programs.length === 0 ? (
            <div className="p-12 text-center text-gray-500">
                Belum ada program kerja yang ditambahkan.
            </div>
        ) : (
          <table className="w-full text-left border-collapse">
            <thead className="bg-white/5 text-gray-400 text-xs uppercase tracking-wider">
              <tr>
                <th className="p-4 border-b border-white/10">Status</th>
                <th className="p-4 border-b border-white/10">Judul Program</th>
                <th className="p-4 border-b border-white/10">Tanggal Dibuat</th>
                <th className="p-4 border-b border-white/10 text-right">Aksi</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-white/10">
              {programs.map((item) => (
                <tr key={item.id} className="hover:bg-white/5 transition-colors">
                  
                  {/* KOLOM STATUS */}
                  <td className="p-4">
                    <span className={cn(
                      "px-2 py-1 rounded text-[10px] font-bold border uppercase",
                      item.status === "PUBLISHED" 
                        ? "bg-green-500/10 text-green-400 border-green-500/20" 
                        : item.status === "REJECTED"
                        ? "bg-red-500/10 text-red-400 border-red-500/20"
                        : "bg-yellow-500/10 text-yellow-400 border-yellow-500/20"
                    )}>
                      {item.status || "PENDING"}
                    </span>
                  </td>
                  
                  <td className="p-4 font-medium text-white">
                    {item.title}
                  </td>
                  <td className="p-4 text-gray-400 text-sm">
                      <div className="flex items-center gap-2">
                        <Calendar size={14} />
                        <span suppressHydrationWarning>
                            {new Date(item.createdAt).toLocaleDateString("id-ID", {
                                day: 'numeric', month: 'short', year: 'numeric'
                            })}
                        </span>
                      </div>
                  </td>
                  
                  {/* KOLOM AKSI */}
                  <td className="p-4 text-right flex justify-end gap-2 items-center">
                    
                    {/* HANYA MUNCUL JIKA SUPER ADMIN & STATUS BELUM PUBLISHED */}
                    {userRole === "SUPER_ADMIN" && item.status !== "PUBLISHED" && (
                      <button 
                        onClick={() => handleStatusChange(item.id, "PUBLISHED")}
                        title="Publish Konten"
                        className="p-2 bg-green-900/30 hover:bg-green-600 text-green-400 hover:text-white rounded-lg transition-colors border border-green-500/20"
                      >
                        <CheckCircle size={16} />
                      </button>
                    )}

                    {/* Tombol Reject (Hanya Super Admin & Masih Pending) */}
                     {userRole === "SUPER_ADMIN" && item.status === "PENDING" && (
                      <button 
                        onClick={() => handleStatusChange(item.id, "REJECTED")}
                        title="Tolak Konten"
                        className="p-2 bg-red-900/30 hover:bg-red-600 text-red-400 hover:text-white rounded-lg transition-colors border border-red-500/20"
                      >
                        <XCircle size={16} />
                      </button>
                    )}

                    {/* Tombol Edit (Opsional - Belum ada fungsinya di kode Anda, tapi UI nya ada) */}
                    {/* <button className="p-2 bg-blue-900/30 text-blue-400 rounded-lg hover:bg-blue-600 hover:text-white border border-blue-500/20">
                      <Pencil size={16} />
                    </button> 
                    */}

                    <button 
                        onClick={() => handleDelete(item.id)} 
                        className="p-2 bg-zinc-800 text-gray-400 border border-white/10 rounded-lg hover:bg-red-900/50 hover:text-red-400 hover:border-red-500/30 transition-all"
                        title="Hapus Program"
                    >
                      <Trash2 size={16} />
                    </button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
      </div>
    </div>
  );
}